html {
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 0px;
  overflow-x: hidden;
}
body {
  font-size: 16;
  background: #fff;
  z-index: 1;
}

.App {
  text-align: center;
}

.App-Header {
  position: relative;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: black;
  top: 6em;
  padding: 0px 10em;
}

header {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

header p {
  font-size: 1em;
  font-weight: 400;
  padding: 2em;
  margin-right: 50em;
  margin: 0;
}

.header button {
  background: black;
  border: 0px;
  padding: 1em 3em;
  transition: all 0.2s ease-out;
  -webkit-transition: all 0.2s ease-out;
  -moz-transition: all 0.2s ease-out;
  -ms-transition: all 0.2s ease-out;
  -o-transition: all 0.2s ease-out;
  cursor: pointer;
}
.header button:hover {
  opacity: 0.8;
}
.header button a {
  font-family: 'Open Sans', sans-serif;
  color: white;
  text-decoration: none;
}
.header-title {
  position: relative;
  font-family: 'Montserrat', sans-serif;
  font-size: 3em;
  text-align: left;
  top: 2em;
  margin-bottom: 2.2em;
}
.subheader {
  float: left;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.subheader > button,
.subheader > a {
  margin-right: 1.5em;
}

.subheader .icon {
  cursor: pointer;
  color: #9451ae;
}

.subheader .icon:hover {
  color: #623176;
}

.subheader .linked {
  color: #0090c0;
}

.subheader .linked:hover {
  color: #00739a;
}

nav {
  position: fixed;
  z-index: 10;
  padding-top: 2em;

  flex: 1;
  background-color: rgba(255, 255, 255, 0.805);
  width: 100vw;
  justify-content: center;
  align-items: flex-end;
  float: right;
}

nav ul {
  float: right;
  margin-right: 10em;
}

nav li {
  display: inline-block;
  cursor: pointer;
}

nav li .link {
  text-decoration: none;
  color: black;
  padding: 1em;
  opacity: 0.5;
  transition: all 0.3s ease-in-out;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
}

li .link:hover {
  opacity: 1;
}

.title {
  font-size: 4em;
}

.Scrollable-Block {
  padding-top: 6em;
}

.about {
  display: grid;
  grid-template-rows: 100px 1fr;
  grid-template-columns: 1fr 1fr;
  gap: 20px 0;
}

.about-title {
  grid-row: 1;
  margin: auto auto;
}
.about-desc {
  grid-row: 2;
  padding: 0em 1em;
  font-size: 1.5em;
}
.about-image {
  grid-row: 1/3;
  height: 50vh;
  margin: auto auto;
}

.projects {
  display: grid;
  grid-template-columns: repeat(3, 3fr);
  width: 80vw;
}

.project {
  margin: 10px;
}

.project p {
  font-size: 1.01em;
}

.project h1 {
  text-align: center;
}

.project-image {
  height: 14.06vw;
  width: 25vw;

  object-fit: fill;
}

.project a {
  text-decoration: none;
  color: black;
  cursor: pointer;
}

.contact {
  width: 80%;
  position: relative;
  text-align: left;
}
.contact h1 {
  font-family: 'Montserrat', sans-serif;
  font-size: 4.5em;
  text-align: left;
}
.amazing-color {
  color: #9352ae;
  opacity: 1;
}

.contact .email {
  color: black;
  font-size: 3em;
}
.contact li {
  display: inline-block;
  margin-top: 0;
  margin-bottom: 0;
  margin-right: 1em;
  margin-left: 0;
}
.contact li a {
  text-decoration: none;
  color: black;
  font-size: 2em;
}

.footer {
  position: relative;
  top: 2em;
  padding: 1em;
}
.footer a {
  text-decoration: none;
  color: #2c59fb;
}
