@media only screen and (min-device-width: 320px) and (max-device-width: 800px) {
  body {
    font-size: 16px;
    width: 100vw;
    overflow-x: hidden;
    margin: 0;
  }
  .header-title {
    font-size: 4em;
    margin-right: 0em;
    top: 0;
    left: 0;
  }
  .App-Header {
    padding: 0.4em;
  }
  .heading-background {
    display: none;
  }
  nav {
    font-size: 2em;
    margin: 0 auto;
    float: none;
    width: 100vw;
    padding: 0;
    padding-top: 0.5em;
  }

  nav ul {
    margin: 0 auto;
    float: none;
    padding-left: 0;
  }
  nav ul li {
    margin: 10px;
    font-size: 14px;
  }

  .header-title {
    font-size: 3em;
    text-align: left;
  }
  .about {
    position: relative;
    display: grid;
    grid-template-columns: 1fr;
    width: 100%;
  }
  .about-title {
    grid-row: 1;
  }
  .about-image {
    grid-row: 2;
    height: 30vh;
  }
  .about-desc {
    grid-row: 3;
  }
  .about-content p {
    font-size: 3.5em;
    text-align: left;
    width: 300px;
  }

  .projects {
    left: 2em;
    top: 2em;
    grid-template-columns: 1fr;
    align-items: flex-start;
  }
  .work .work-content {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 5em;
  }
  .project img {
    height: 42.18vw;
    width: 75vw;
  }

  .contact {
  }
  .contact h1 {
    font-size: 4em;
  }
  .contact .email {
    font-size: 1.4em;
  }

  .contact li {
    display: block;
    padding: 2em;
  }
  .contact li a {
    font-size: 2.5em;
  }
  .footer {
    top: 4em;
    padding: 1em;
    font-size: 2em;
  }
}

